html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5,
h6, p, blockquote, pre, a, abbr, acronym, address, big,
cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small,
strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt,
dd, ol, ul, li, fieldset, form, label, legend, table, caption,
tbody, tfoot, thead, tr, th, td, article, aside, canvas, details,
embed, figure, figcaption, footer, header, hgroup, menu, nav,
output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline;
}

html {
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

img, fieldset, a img {
  border: none;
}

input[type="text"],
input[type="email"],
input[type="tel"],
textarea {
  -webkit-appearance: none;
}

input[type="submit"],
button {
  cursor: pointer;
}

input[type="submit"]::-moz-focus-inner,
button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

textarea {
  overflow: auto;
}

input, button {
  margin: 0;
  padding: 0;
  border: 0;
}

div, input, textarea, select, button,
h1, h2, h3, h4, h5, h6, a, span, a:focus {
  outline: none;
}

ul, ol {
  list-style-type: none;
}

table {
  border-spacing: 0;
  border-collapse: collapse;
  width: 100%;
}

html {
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

@media (prefers-reduced-motion: reduce) {
  * {
    transition: none !important;
  }
}

.icon-facebook {
  width: 0.53em;
  height: 1em;
  fill: #000;
}

html {
  font-size: 10px;
}

body {
  background: #fff url(../img/bg.jpg);
  font-family: Helvetica, Arial, sans-serif;
}

a {
  text-decoration: none;
  color: #b9b9b9;
}

a:hover {
  color: #e8603c;
}

.wrapp {
  max-width: 960px;
  margin: 1rem auto;
  padding-left: 1rem;
  padding-right: 1rem;
}

.h2__link {
  color: #e8603c;
  text-transform: uppercase;
}

.h2 {
  font-family: 'Bebas', sans-serif;
  font-weight: 100;
  font-size: 2.4rem;
  text-transform: uppercase;
}

.h3 {
  font-family: 'Bebas', sans-serif;
  font-weight: 100;
  font-size: 1.4rem;
  color: #e8603c;
  margin-bottom: 0.5rem;
}

.text {
  font-family: Helvetica;
  font-weight: 400;
  color: #414141;
  line-height: 1.5;
  font-size: 1.2rem;
}

.link {
  font-size: 1.2rem;
  color: #b9b9b9;
}

.clearfix::after {
  content: '';
  display: table;
  clear: both;
}

@font-face {
  font-family: 'Bebas';
  src: url("../fonts/BebasNeue.woff2") format("woff2"), url("..fonts/BebasNeue.woff") format("woff");
  font-weight: 400;
  font-style: normal;
}

.header {
  font-family: 'Bebas', sans-serif;
  font-weight: 400;
  text-align-last: justify;
  white-space: nowrap;
}

.header__logo {
  font-size: 3.6rem;
  text-transform: uppercase;
  color: #e8603c;
}

.header__logo-span {
  color: #f3aa97;
}

.header__logo:hover span {
  color: #e8603c;
}

.nav {
  display: inline-block;
  font-size: 2rem;
}

.nav li {
  display: inline-block;
  padding-left: 2.8rem;
}

.nav li a {
  text-transform: uppercase;
}

.hero {
  overflow: hidden;
}

.hero .block-title {
  background: #e8603c;
  padding: 1rem;
  color: #fff;
  letter-spacing: 2px;
  position: relative;
}

.hero .block-title:before {
  content: "";
  background: #f3aa97;
  position: absolute;
  width: 1000px;
  right: 100%;
  margin-right: 10px;
  height: 100%;
  top: 0;
}

.hero .block-title:after {
  content: "";
  background: #f3aa97;
  position: absolute;
  width: 1000px;
  left: 100%;
  margin-left: 10px;
  height: 100%;
  top: 0;
}

.posts {
  display: inline-block;
  width: 67rem;
}

.posts .post {
  margin-bottom: 10rem;
  position: relative;
}

.posts .post__block {
  width: 7.5rem;
  height: 7.5rem;
  position: absolute;
  background: #e8603c;
  top: 0;
  left: -8.5rem;
  display: flex;
  justify-content: center;
}

.posts .post__block img {
  align-self: center;
}

.posts .post__img {
  margin-bottom: 2rem;
}

.posts .post__data {
  display: inline-block;
  margin-right: 4rem;
  vertical-align: top;
}

.posts .post__desc {
  display: inline-block;
}

.posts .post__desc-header {
  margin-bottom: 1rem;
  font-size: 1.6rem;
}

.posts .post__desc-text {
  font-size: 1.2rem;
  margin-bottom: 1rem;
  line-height: 1.5;
}

.posts .post::after {
  content: "";
  position: absolute;
  height: 14px;
  width: 100%;
  left: 0;
  bottom: -5rem;
  background: url(../img/line.png) repeat;
}

.posts .post__desc {
  display: inline-block;
  max-width: 78%;
}

.posts .post__desc_all-width {
  max-width: 67rem;
}

.posts .post__desc_header {
  margin-bottom: 2rem;
  font-size: 1.8rem;
  letter-spacing: 3px;
}

.posts .post__link {
  margin-top: -1.5rem;
  margin-bottom: 2.5rem;
}

.posts__pag {
  margin-bottom: 3rem;
}

.blockquote {
  position: relative;
  max-width: 67rem;
  height: auto;
  background-color: #f5f5f5;
  padding: 2rem 3rem 2rem 8rem;
}

.blockquote::before {
  position: absolute;
  top: 2rem;
  left: 4rem;
  content: url(../img/before3-3.png);
}

.blockquote__text {
  font-style: italic;
}

.pag__item {
  display: inline-block;
  font-size: 1.4rem;
  line-height: 2.6rem;
  text-align: center;
  width: 2.6rem;
  height: 2.6rem;
  background: #e8603c;
  color: #fff;
}

.pag__item:hover {
  background: #f3aa97;
}

.sitebar {
  display: inline-block;
  width: 27rem;
  height: auto;
  padding-left: 5rem;
  float: right;
  vertical-align: top;
}

.sitebar__line {
  background: url(../img/line.png) repeat;
  width: 100%;
  height: 14px;
  margin-bottom: 2rem;
}

.sitebar__oneitem {
  margin-bottom: 4rem;
}

.sitebar__oneitem_header {
  font-size: 1.4rem;
  letter-spacing: 2px;
  display: inline-block;
  margin-top: -0.25rem;
  padding-right: 1rem;
  background: url(../img/bg.jpg) repeat;
}

.sitebar__oneitem .photo {
  width: 5rem;
  height: 5rem;
  background-color: #f3aa97;
  border: 2px solid #e8603c;
  display: inline-block;
  margin: 0.1rem;
}
